import React, { useEffect, useState, useCallback } from 'react';
import {useViewportGrid} from '@ohif/ui';
import { useToolbar } from '@ohif/core';
import * as csTools from '@cornerstonejs/tools';
const { Enums: csToolsEnums } = csTools
const { Events } = csToolsEnums
const { state } = csTools

import BrushToolEdit from "./brushToolEdit";
import CircleScissorsToolEdit from "./cscissorsToolEdit";
import PaintFillToolEdit from "./paintFillToolEdit";
import RectangleROIStartEndThresholdToolEdit from "./rectangleROIStartEndToolEdit";
import RectangleROIThresholdToolEdit from "./rectangleROIToolEdit";
import RectangleScissorsToolEdit from "./rscissorsToolEdit";
import SphereScissorsToolEdit from "./sscissorsToolEdit";
import UndoToolEdit from "./undoToolEdit";

const SEG_TOOL_NAMES = [
    'Brush',
    'CircleScissor',
    'PaintFill',
    'RectangleROIStartEndThreshold',
    'RectangleROIThreshold',
    'RectangleScissor',
    'SphereScissor'
]

export default function BaseToolEdit (
    {
        segmentationService,
        toolbarService,
        toolGroupService,
        cornerstoneViewportService,
        commandsManager
    }){
    // @ts-ignore
    const [{ activeViewportId, viewports }, viewportGridService] = useViewportGrid();
    const [activeSegTool, setActiveSegTool] = useState(null)

    const searchActiveSegTool = () => {
        const toolStates = state.toolGroups.filter(tg => tg.id === 'mpr')[0]?.toolOptions
        if(!toolStates) setActiveSegTool(null)
        const toolStatesArray = Object.keys(toolStates).map(function (key) {
            return [key, toolStates[key]];
        });
        let activeSegTool = false
        toolStatesArray.forEach(tool => {
            //Check if tool is a segmentation tool
            if (SEG_TOOL_NAMES.includes(tool[0])){
                //Check if tool is active
                if(tool[1].mode === 'Active'){
                    activeSegTool = true
                    setActiveSegTool(tool[0])
                    return;
                }
            }
        })
        if(!activeSegTool) setActiveSegTool(null)
    }

    useEffect(() => {
        searchActiveSegTool()
    },[])

    useEffect(() => {
        const { unsubscribe } = toolbarService.subscribe(
            toolbarService.EVENTS.TOOL_BAR_MODIFIED,
            searchActiveSegTool
        );

        return () => {
            unsubscribe();
        };
    }, [toolbarService]);

    const renderActiveTool = () => {
        switch (activeSegTool){
            case 'Brush':
                return <BrushToolEdit
                    cornerstoneViewportService={cornerstoneViewportService}
                    segmentationService={segmentationService}
                />
            case 'CircleScissor':
                return <CircleScissorsToolEdit
                    cornerstoneViewportService={cornerstoneViewportService}
                    segmentationService={segmentationService}
                />
            case 'PaintFill':
                return <PaintFillToolEdit />
            case 'RectangleROIStartEndThreshold':
                return <RectangleROIStartEndThresholdToolEdit
                    segmentationService={segmentationService}
                    cornerstoneViewportService={cornerstoneViewportService}
                    commandsManager={commandsManager}
                />
            case 'RectangleROIThreshold':
                return <RectangleROIThresholdToolEdit
                    segmentationService={segmentationService}
                    cornerstoneViewportService={cornerstoneViewportService}
                    commandsManager={commandsManager}
                />
            case 'RectangleScissor':
                return <RectangleScissorsToolEdit
                    cornerstoneViewportService={cornerstoneViewportService}
                    segmentationService={segmentationService}
                />
            case 'SphereScissor':
                return <SphereScissorsToolEdit
                    cornerstoneViewportService={cornerstoneViewportService}
                    segmentationService={segmentationService}
                />
            default:
                return null
        }
    }

    return (
        <>
            {activeSegTool && (
                <UndoToolEdit
                    cornerstoneViewportService={cornerstoneViewportService}
                    segmentationService={segmentationService}
                />
            )}
            {renderActiveTool()}
        </>
    )
}