import {Button, useViewportGrid, Icon} from "@ohif/ui";
import React, {useState} from "react";
import classNames from "classnames";
import {Segmentation} from "@ohif/extension-cornerstone/src/services/SegmentationService/SegmentationServiceTypes";
import PropTypes from "prop-types";
import {saveAs, FileSaver} from 'file-saver'
import { segmentation as cstSegmentation } from '@cornerstonejs/tools';

function SegmentsConfigurationImportExport({segmentationService, segmentations}) : React.JSX.Element {
    const [isMinimized, setIsMinimized] = useState(false);
    const [{ activeViewportId, viewports }, viewportGridService] = useViewportGrid();

    const exportConfiguration = () : void => {
        const json = JSON.stringify(segmentations[0].segments)
        const json_blob = new Blob([json], {type: "text/plain;charset=utf-8"});
        saveAs(json_blob, 'segments_configuration.json')
    }

    const importConfiguration = () : void => {
        console.log('SEGS AT IMPORT', segmentations)
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = 'application/json'
        input.onchange = _ => {
            const json_file = Array.from(input.files)[0];
            const reader = new FileReader();
            reader.onload = function(event) {
                try {
                    if (typeof event.target.result === "string") {
                        for(let i = 1; i < segmentations[0].segments.length; i++){
                            segmentationService.removeSegment(segmentations[0].id, i)
                        }

                        const jsonContent = JSON.parse(event.target.result);
                        jsonContent.forEach(segment => {
                            if(segment){
                                const {segmentIndex} = segment
                                segmentationService.addSegment(segmentations[0].id, {
                                    segmentIndex: segmentIndex,
                                    toolGroupId: viewports.get(activeViewportId).viewportOptions.toolGroupId,
                                    properties: segment,
                                    override: true
                                });
                            }
                        })
                    }
                    else {
                        console.error("ERROR::JSON_READING::CORRUPTED_CONTENT");
                    }
                } catch (error) {
                    console.error("ERROR::JSON_READING", error);
                }
            };
            reader.readAsText(json_file);
        };
        input.click();
    }

    return (
        <div className="flex flex-col min-h-0 font-inter font-[300]">
            <div className="bg-primary-dark">
                <div
                    className="flex cursor-pointer items-center"
                    onClick={evt => {
                        evt.stopPropagation();
                        setIsMinimized(!isMinimized);
                    }}
                >
                    <Icon
                        name="panel-group-open-close"
                        className={classNames(
                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                            {
                                'transform rotate-90': !isMinimized,
                            }
                        )}
                    />
                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                        {'Segments Configuration I/O'}
                    </span>
                </div>
                {/* active segmentation */}
                {!isMinimized && (
                    <div className="pl-2 pr-2 pb-[9px] pt-[9px]" style={{display:'flex', flexDirection:"column"}}>
                        <div
                            className="text-[#d8d8d8] text-[12px] font-[300] justify-evenly inline-flex"
                        >
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={importConfiguration}
                            >{'Import File'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={exportConfiguration}
                            >{'Export File'}</Button>
                        </div>
                    </div>
                )}
                <div className="h-[6px] bg-black "></div>
            </div>
        </div>
    )
}

SegmentsConfigurationImportExport.propTypes = {
    segmentations: PropTypes.array.isRequired
}

export default SegmentsConfigurationImportExport
